<template>
  <div class="metadata-management-page">
    <div class="content-header">
      <bread-crumb current-level="元数据管理">
        <div
          slot="header"
          class="bread-crumb-metadata-header"
        >元数据管理
          <span>
            <a
              @click="refreshData"
            >
              <i
                :class="{'animation-rotate': refreshCache}"
                class="iconfont icon-shuaxin2"
              />
            </a>
          </span>
        </div>
      </bread-crumb>
    </div>
    <div class="content-body">
      <div class="filter-content">
        <gs-button
          type="primary"
          @click="toCreateView"
        >新建离线表</gs-button>
        <gs-button
          class="pull-right expand-all"
          type="text-primary"
          @click="closeAll"
        >全部收起</gs-button>
        <div class="search-filter">
          <gs-search
            :disabled="refreshCache"
            v-model="filter.keyword"
            placeholder="请输入搜索内容..."
            @search="handleSearch"
          >
            <template slot="prepend">
              <gs-select
                v-model="filter.type"
                :disabled="refreshCache"
              >
                <gs-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                />
              </gs-select>
            </template>
          </gs-search>
        </div>
      </div>
      <div
        v-loading="table.isLoading || refreshCache"
        v-if="showViewType === 0"
        class="relative db-list"
      >
        <el-table
          ref="table"
          :row-class-name="getClassName"
          :data="table.data"
          stripe
        >
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-table
                :show-header="false"
                :row-class-name="getClassName"
                :data="props.row.showTableList"
                stripe
              >
                <el-table-column
                  prop="filename"
                  label="数据名称"
                  show-overflow-tooltip
                >
                  <template
                    slot-scope="scope"
                    class="cell el-tooltip"
                  >
                    <i
                      class="iconfont icon-biaoge icon-item"
                    />
                    <span style="margin-left: 4px">{{ scope.row.tableName }}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  prop="description"
                  label="描述"
                  class-name="expand-second-column"
                  show-overflow-tooltip
                />
                <el-table-column
                  align="center"
                  prop="user"
                  label="操作"
                  width="80"
                >
                  <template slot-scope="scope">
                    <gs-button
                      type="text-primary"
                      @click="toDetail(scope.row.tableId)"
                    >详情</gs-button>
                  </template>
                </el-table-column>
              </el-table>
              <div
                v-if="props.row.showTableList.length < props.row.tableList.length"
                class="text-center show-more"
              >
                <gs-button
                  @click="showMore(props.row)"
                >点击加载更多</gs-button>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            label="数据名称"
            show-overflow-tooltip
          >
            <template slot-scope="scope">
              <i
                class="iconfont icon-yuanshuju"
              />
              <span style="margin-left: 4px">{{ scope.row.databaseName }}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="描述"
            prop="databaseDescription"
            show-overflow-tooltip
          />
          <el-table-column
            align="center"
            label="操作"
            width="80"
          >
            <template slot-scope="scope">
              <span>-</span>
            </template>
          </el-table-column>
        </el-table>
        <gs-pagination
          v-show="table.total>0"
          :current="table.currentPage"
          :total="table.total"
          :page-size="table.pageSize"
          :page-sizes="table.pageSizes"
          layout="range,total,pagesizes,pager,jumper,jumpbtn"
          @page-change="pageChange"
          @page-size-change="pageSizeChange"
        />
      </div>
      <table-filter
        v-if="showViewType === 1"
        ref="table-filter"
        :refresh-cache="refreshCache"
      />
      <field-filter
        v-if="showViewType === 2"
        ref="field-filter"
        :refresh-cache="refreshCache"
      />
    </div>
  </div>
</template>

<script>
import './style';
import TableFilter from './table-filter.vue';
import FieldFilter from './field-filter.vue';
import BreadCrumb from 'src/components/breadcrumb';
import * as metadataManagementService from 'src/service/metadata-management/metadata-management-list';
import * as cacheService from 'src/service/cache';
// 表每次限制展示的最大行数
const LIMIT_LINES = 10;
// 是否需要显示缓存界面
let keepAlive = false;
export default {
  name: 'MetadataManagement',
  components: {
    TableFilter,
    FieldFilter,
    BreadCrumb
  },
  data() {
    return {
      refreshCache: false, // 强制刷新缓存
      showViewType: 0, // 0 库列表  1 按表搜索列表 2 按字段搜索列表
      filter: {
        // 搜索关键词
        keyword: '',
        // 类型
        type: 0
      },
      options: [
        {
          value: 0,
          label: '库'
        },
        {
          value: 1,
          label: '表'
        },
        {
          value: 2,
          label: '字段'
        }
      ],
      table: {
        isLoading: false,
        data: [
        ],
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [10, 20, 30, 40, 50, 100]
      }
    };
  },
  /**
   * 进入页面触发
   */
  beforeRouteEnter(to, from, next) {
    // 获取当前访问是否需要显示缓存页面
    keepAlive = to.meta.keepAlive;
    next();
  },
  /**
   * 路由跳转前触发
   */
  beforeRouteLeave(to, from, next) {
    // 设置不需要缓存
    from.meta.keepAlive = false;
    next();
  },
  /**
   *keep-alive的钩子函数
   */
  activated() {
    // 判断不需要缓存情况下刷新
    if (keepAlive === false) {
      this.showViewType = 0;
      this.filter.keyword = '';
      this.filter.type = 0;
      this.table.data = [];
      this.table.total = 0;
      this.table.currentPage = 1;
      this.table.pageSize = 10;
      this.init();
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    /**
     * 初始化
     */
    init() {
      this.getMetaList(true);
    },
    /**
     * 获取列类名
     */
    getClassName({row}) {
      return row.status === 'DELETED' ? 'delete-status' : '';
    },
    /**
     * 收起全部表格
     */
    closeAll() {
      this.table.data.forEach(item => {
        this.$refs['table'].toggleRowExpansion(item, false);
      });
    },
    /**
     * 跳转到新建页面
     */
    toCreateView() {
      this.$router.push({
        path: '/main/metadata/create'
      });
    },
    /**
     * 跳转到详情界面
     */
    toDetail(tableId) {
      this.$router.push({
        path: `/main/metadata/detail/${tableId}`
      });
    },
    /**
     * 处理没有子元素的不展示展开图标
     */
    handleExpandShow() {
      this.$nextTick(() => {
        // 判断节点是否存在（快速切换路由，会导致页面销毁，该方法才执行的情况）
        if (!this.$refs['table']) {
          return;
        }
        const trs = [].slice.call(this.$refs['table'].$el.querySelectorAll('.el-table__row'));
        this.table.data.forEach((row, index) => {
          if (!row.tableList.length) {
            trs[index].querySelector('.el-table__expand-column .cell').style.visibility = 'hidden';
          } else {
            trs[index].querySelector('.el-table__expand-column .cell').style.visibility = 'visible';
          }
        });
      });
    },
    /**
     * 强制刷新页面
     */
    refreshData() {
      if (this.refreshCache) {
        return;
      }
      this.refreshCache = true;
      cacheService.refreshDataCache().then((res) => {
        this.handleSearch();
      }).catch((res) => {
        this.refreshCache = false;
      });
    },
    /**
     * 搜索逻辑处理
     */
    handleSearch() {
      switch (this.filter.type) {
        // 按库搜索列表
        case 0:
          this.showViewType = 0;
          this.getMetaList(!this.refreshCache, this.refreshCache).then(res => {
            this.refreshCache = false;
          });
          break;
        // 按表搜索列表
        case 1:
          this.showViewType = 1;
          this.$nextTick(() => {
            this.$refs['table-filter'].getListByTable(this.filter.keyword, this.refreshCache).then(res => {
              this.refreshCache = false;
            });
          });
          break;
        // 按字段搜索列表
        case 2:
          this.showViewType = 2;
          this.$nextTick(() => {
            this.$refs['field-filter'].getListByField(this.filter.keyword, this.refreshCache).then(res => {
              this.refreshCache = false;
            });
          });
          break;
      }
    },
    /**
     * 查询列表
     * reload 是否重新加载，将查询页数置为1
     */
    getMetaList(reload, refreshCache) {
      this.table.isLoading = true;
      if (reload) {
        this.table.currentPage = 1;
      }
      const queryKeyword = this.filter.keyword;
      return metadataManagementService.getMetadataList({
        query: this.filter.keyword,
        offset: (this.table.currentPage - 1) * this.table.pageSize,
        limit: this.table.pageSize
      }).then((res) => {
        // 重复请求处理
        if (this.filter.keyword !== queryKeyword) {
          return;
        }
        this.table.isLoading = false;
        // 设置总条数，根据返回数组数量是否等于页面选择显示数量+1，判断是否还有下一页
        this.table.total = res.sum;
        // 处理数据表前端分页逻辑
        res.lists.forEach(row => {
          row.showTableList = row.tableList.slice(0, LIMIT_LINES);
        });

        this.table.data = res.lists;
        this.handleExpandShow();
      }).catch((res) => {
        this.table.isLoading = false;
      });
    },
    /**
     * 点击展开更多逻辑处理
     */
    showMore(row) {
      if (row.showTableList.length < row.tableList.length) {
        row.showTableList.push(...row.tableList.slice(row.showTableList.length, row.showTableList.length + LIMIT_LINES));
      }
    },
    /**
     * 当前页改变事件
     */
    pageChange(newPage) {
      this.table.currentPage = newPage;
      this.getMetaList();
    },
    /**
     * 每页显示条数改变事件
     */
    pageSizeChange(newPageSize) {
      this.table.pageSize = newPageSize;
      this.table.currentPage = 1;
      this.getMetaList();
    }
  }
};
</script>




