<template>
  <div class="metadata-management-field-filter-page">
    <div
      v-loading="table.isLoading || refreshCache"
      class="relative"
    >
      <el-table
        ref="table"
        :row-class-name="getClassName"
        :data="table.data"
      >
        <el-table-column class-name="cell-filter-item">
          <template slot-scope="scope">
            <div>
              <a @click="toTableFieldDetail(scope.row.tableId)">
                <i
                  class="iconfont icon-ziduantubiao"
                />
                <span style="margin-left: 4px">{{ scope.row.columnName }}</span>
              </a>
            </div>
            <div>
              <i
                class="iconfont icon-yuanshuju"
              />
              <span style="margin-left: 4px">{{ scope.row.databaseName }}</span>
              <i
                :style="{transform: 'rotate(-90deg)'}"
                class="gs-icon-down"
              />
              <i
                class="iconfont icon-biaoge icon-item"
              />
              <span style="margin-left: 4px">{{ scope.row.tableName }}</span>
              <i
                :style="{transform: 'rotate(-90deg)'}"
                class="gs-icon-down"
              />
              <i
                class="iconfont icon-ziduantubiao icon-item"
              />
              <span style="margin-left: 4px">{{ scope.row.columnName }}</span>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <gs-pagination
        v-show="table.total>0"
        :current="table.currentPage"
        :total="table.total"
        :page-size="table.pageSize"
        :page-sizes="table.pageSizes"
        layout="range,total,pagesizes,pager,jumper,jumpbtn"
        @page-change="pageChange"
        @page-size-change="pageSizeChange"
      />
    </div>

  </div>
</template>

<script>
import './table-filter.scss';
import * as metadataManagementService from 'src/service/metadata-management/metadata-management-list';
export default {
  props: {
    refreshCache: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      searchText: null,
      table: {
        isLoading: false,
        data: [],
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [10, 20, 30, 40, 50, 100]
      }
    };
  },
  mounted() {
  },
  methods: {
    /**
     * 获取列类名
     */
    getClassName({row}) {
      return row.status === 'DELETED' ? 'delete-status' : '';
    },
    /**
     * 跳转到表详情
     */
    toTableFieldDetail(tableId) {
      this.$router.push({
        path: `/main/metadata/detail/${tableId}`,
        query: {
          module: 'field'
        }
      });
    },
    /**
     * 供父组件调用查询方法
     */
    getListByField(searchText, refreshCache) {
      this.searchText = searchText;
      return this.getList(!refreshCache, refreshCache);
    },
    /**
     * 获取列表
     * @reload 是否重新加载，将查询页数置为1
     */
    getList(reload, refreshCache = false) {
      this.table.isLoading = true;
      if (reload) {
        this.table.currentPage = 1;
      }
      const queryKeyword = this.searchText;
      return metadataManagementService.getColumnList({
        query: this.searchText,
        offset: (this.table.currentPage - 1) * this.table.pageSize,
        limit: this.table.pageSize
      }).then((res) => {
        // 重复请求处理
        if (this.searchText !== queryKeyword) {
          return;
        }
        this.table.isLoading = false;
        this.table.total = res.sum;
        this.table.data = res.lists;
      }).catch((res) => {
        this.table.isLoading = false;
      });
    },
    /**
     * 当前页改变事件
     */
    pageChange(newPage) {
      this.table.currentPage = newPage;
      this.getList();
    },
    /**
     * 每页显示条数改变事件
     */
    pageSizeChange(newPageSize) {
      this.table.pageSize = newPageSize;
      this.table.currentPage = 1;
      this.getList();
    }
  }
};
</script>




